<template>
<div >
   <mu-appbar class="fixed" title="">
     <mu-icon-button icon="arrow_back" slot="left" @click="goback()" />
   </mu-appbar>
<div style="padding-top:66px;">
 <mu-card>
   <mu-card-media >
    <div id="player" class="player">
          <video-player  class="vjs-custom-skin"
                         ref="videoPlayer"
                         :options="playerOptions"
                         :playsinline="true">
                       
          </video-player>
    </div>
   
  
  </mu-card-media>
 </mu-card>
 <div id="chatview" style="position:absolute;top:0;bottom:0;left:0;right:0;">
      <iframe :src="chaturl" width="100%" height="100%"></iframe>
  </div>
</div>
<mu-dialog :open="dialog" title="">
    <h3></h3>
    <mu-flat-button  label="" slot="actions" primary @click="()=>{dialog=false}"/>
  </mu-dialog>
</div>
</template>
<script >
  require('videojs-contrib-hls/dist/videojs-contrib-hls')

	export default {
		name:'livevideo',

    mounted(){

        this.setplayer();
    },
		
		data(){
			return {
				currentTab:'tab1',
        dialog:false,
        videoplayerurl:'',
       
        playerOptions:null
			}
		},
   
    beforeRouteEnter(to,from,next)
    {
      next(vm=>{
        vm.setplayer()
      })
    },

    beforeRouteLeave(to,from,next)
    {
       this.playerOptions=null;
       next(true)
     

    },
		computed:{
       videoObject() {
        return this.$store.state.liveobj
       },
        chaturl(){
        if(this.$store.state.loginedUser){
          return "http://104.201.54.66:8000?role=user&username="+this.$store.state.loginedUser.username
        }
        
        return "http://104.201.54.66:8000?role=guest&username="+Math.floor(Math.random()*100000)
      }
    },
		methods: {
     
      setplayer(){
           this.$http.get(this.$store.state.serverUrl+"/live/?name="+this.videoObject.alt).then(response=>{
            
            var slist=[]
            slist.push({type:"application/x-mpegURL",src:response.body,withCredentials: false})
           
            this.playerOptions={
              aspectRatio:'16:9',
              sources:slist,
              controlBar: {
              timeDivider: false,
              durationDisplay: false
          },       
            }
            //alert(slist[0].src)
        document.getElementById("chatview").style.top=66+document.getElementById("player").offsetWidth*9/16+"px"

           
          },response=>{
        
          })

      },
     
			fetchData () {
				alert('hello')
			},
      handleTabChange (val) {
        this.currentTab = val
      },
      goback()
      {
        this.$router.go(-1)
      }
     

    } 
  }
</script>
<style scoped>
  
   .flex{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
   }
   
   .desc-container{
    height:100%;
    overflow-y: scroll;
   }
   img {
    width: 100%;
   }
   .fixed {
    position:fixed;
    top: 0;
   }
 
</style>


